import React from 'react';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import Styles from './Table.module.css';

class Countries extends React.Component{

    render(){
        const { countriesData } = this.props;

        return(
            <TableRow>
                <TableCell className={Styles.country}>{countriesData.Country}</TableCell>
                <TableCell className={Styles.confirm}>{countriesData.NewConfirmed}</TableCell>
                <TableCell className={Styles.recover}>{countriesData.TotalRecovered}</TableCell>
                <TableCell className={Styles.death}>{countriesData.TotalDeaths}</TableCell>
            </TableRow>
        )
    }

}

export default Countries;