import React from 'react';
import axios from 'axios';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Countries from './Countries';
import Styles from './Table.module.css';

class Tables extends React.Component{

    state={
        countriesData : [],
        global: [],
        currentDate: null,
        loading: true,
    }

    async componentDidMount(){
        const res = await axios.get(" https://api.covid19api.com/summary");

        this.setState({countriesData: res.data.Countries});
        this.setState({global: res.data.Global});
        this.setState({currentDate: res.data.Date});
        this.setState({loading: false});

    }
    render(){

        if(this.state.loading){
            return(
                <h1>Loading...</h1>
            )
        }
        return(
            <>
            <div className={Styles.header}>
                <p>Covid-<span className={Styles.span}>19</span></p>
            </div>
                <div>
                    <TableContainer className={Styles.container}>
                        <Table>
                            <TableHead>
                                <TableRow>
                                    <TableCell className={Styles.head}>Country</TableCell>
                                    <TableCell className={Styles.head}>New Confirmed</TableCell>
                                    <TableCell className={Styles.head}>Total Recovered</TableCell>
                                    <TableCell className={Styles.head}>Total Deaths</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {this.state.countriesData.map(country => (
                                    <Countries countriesData={country} key={country.Country}/>
                                ))}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </div>
            </>
        )
    }
    
}

export default Tables;