import React from 'react';
import { Card, CardContent, Typography, Grid, } from '@material-ui/core';
import Styles from './Cards.module.css';
import CountUp from 'react-countup';
import cx from 'classnames';

const Cards = ({ data : { confirmed, recovered, deaths, lastUpdate } }) => {
    
    if(!confirmed){
        return 'Loading...';
    }

    return(
        <>
        <div className={Styles.container}>
            <Grid container spacing={1} justify="center">
                <Grid item component={Card} xs={12} md={3} className={cx(Styles.card, Styles.confirmed)}>
                    <CardContent >
                        <Typography gutterbuttom className={Styles.confi}>Confirmed</Typography>
                        <Typography className={Styles.cval} varient="h5">
                            <CountUp
                                start={0}
                                end={confirmed.value}
                                duration={2.5}
                                separator=","
                            />
                        </Typography>
                        <Typography className={Styles.date}>{new Date(lastUpdate).toDateString()}</Typography>
                        <Typography className={Styles.para}>Number of Confirmed Covid-19 cases</Typography>
                    </CardContent>
                </Grid>

                <Grid item component={Card} xs={12} md={3} className={cx(Styles.card, Styles.recovered)}>
                    <CardContent>
                        <Typography gutterbuttom className={Styles.confi}>Recovered</Typography>
                        <Typography className={Styles.rval} varient="h5">
                        <CountUp
                                start={0}
                                end={recovered.value}
                                duration={2.5}
                                separator=","
                            />
                        </Typography>
                        <Typography className={Styles.date}>{new Date(lastUpdate).toDateString()}</Typography>
                        <Typography className={Styles.para}>Number of people Recovered from Covid-19</Typography>
                    </CardContent>
                </Grid>

                <Grid item component={Card} xs={12} md={3} className={cx(Styles.card, Styles.deaths)}>
                    <CardContent>
                        <Typography className={Styles.confi} gutterbuttom>Deaths</Typography>
                        <Typography className={Styles.dval} varient="h5">
                        <CountUp
                                start={0}
                                end={deaths.value}
                                duration={2.5}
                                separator=","
                            />
                        </Typography>
                        <Typography className={Styles.date}>{new Date(lastUpdate).toDateString()}</Typography>
                        <Typography className={Styles.para}>Number of Deaths caused by Covid-19</Typography>
                    </CardContent>
                </Grid>
            </Grid>
        </div>
        </>
    );
}

export default Cards;